<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="materialGroupCode">
          <el-select
            v-model="query.materialGroupCode"
            filterable
            remote
            placeholder="物料组编号"
            :remote-method="remoteMethod"
            :loading="selectLoading"
            class="filter-item"
            style="width: 178px;"
            clearable
            @clear="options = []"
          >
            <el-option
              v-for="item in options"
              :key="item.code"
              :label="item.code"
              :value="item.code"
            />
          </el-select>
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="initTreeMixins"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button>
      </el-form>
    </div>
    <!-- 拖拽屏幕 -->
    <drag-screen :extra-minus-height="40" :left-weight="6">
      <template v-slot:left>
        <div>
          <el-row class="connection-line">
            <el-tree
              :ref="treeRefName"
              v-loading="treeLoadingMixins"
              :data="treeDataMixins"
              :indent="0"
              :props="defaultPropsMixins"
              highlight-current
              :check-strictly="true"
              node-key="id"
              :lazy="false"
              :expand-on-click-node="false"
              :default-expanded-keys="defaultExpandKeysMixins"
              :current-node-key="defaultCheckedNodeMixins"
              @node-expand="handleNodeExpand"
              @node-collapse="handleNodeCollapse"
              @node-click="handleNodeClick"
            >
              <span slot-scope="{ data }" class="custom-tree-node">
                <!-- 根节点 -->
                <template>
                  <span>{{ `${data.code} ${data.description}` }}</span>
                </template>
              </span>
            </el-tree>
          </el-row>
        </div>
      </template>
      <template v-slot:right>
        <right
          ref="rightView"
          :node="nodeInfo"
          :level="nodeLevel"
          @refreshTree="handleRefreshTree"
          @setPrevNode="setPrevNode"
        />
      </template>
    </drag-screen>
    <!-- 新增抽屉 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="dialogType"
      :title="dialogTitle"
      @submit="handleRefreshTree"
    />
  </div>
</template>
<script>
import DragScreen from '@/components/DragScreen'
import { getPullListItemApi, getAllTreeDataApi } from '@/apis/modules/base/baseItemGroup'
import EditDialog from './Dialog'
import Right from './Right'
import { noCacheTreeMixins } from './tree.mixin'
import { requestWrapper } from '@/utils/utils'
export default {
  name: 'WMSBaseItemGroup',
  components: {
    EditDialog,
    DragScreen,
    Right
  },
  mixins: [noCacheTreeMixins],
  data() {
    return {
      query: {
        materialGroupCode: ''
      },
      dialogForm: {},
      dialogType: 'add',
      dialogVisible: false,
      selectLoading: false, // 下拉选loading
      options: [], // 物料组下拉选数据
      nodeInfo: {}, // 当前点击树节点信息
      nodeLevel: 1 // 当前点击树节点等级
    }
  },
  computed: {
    dialogTitle() {
      if (this.dialogType === 'add') {
        return `新增物料组`
      } else {
        return `编辑物料组`
      }
    }
  },
  activated() {
    console.log('物料组页面激活', 111111111111111111111111)
    // this.initTreeMixins()
  },
  mounted() {
    console.log('物料组页面加载完毕', 222222222222222222222222)
    this.initTreeMixins()
  },
  methods: {
    // 初始化树节点数据
    initTreeMixins() {
      return new Promise((resolve, reject) => {
        getAllTreeDataApi({ ...this.query }).then(res => {
          const { code, data } = res
          if (!code) {
            this.treeDataMixins = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 点击节点事件
    handleNodeClick(data, node, comSelf) {
      console.log('节点数据：', data, node)
      this.nodeLevel = node.level
      this.nodeInfo = { ...data }
    },
    // 新增
    handleAdd() {
      this.dialogType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 下拉框输入值发生变化时调用
    async remoteMethod(query) {
      this.options = []
      if (query !== '') {
        this.selectLoading = true
        const res = await requestWrapper(getPullListItemApi({
          materialGroupCode: query
        }))
        if (!res.success) return
        this.selectLoading = false
        this.options = res.data
      } else {
        this.options = []
      }
    },
    // 表单重置
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.defaultExpandKeysMixins = []
      this.initTreeMixins()
    }
  }
}
</script>

