/*
*   获取树，非懒加载
*/
export const noCacheTreeMixins = {
  data() {
    return {
      treeRefName: 'tree',
      defaultPropsMixins: {
        children: 'children',
        label: 'name',
        isLeaf: (data) => {
          return !data.hasleaf
        }
      },
      treeLoadingMixins: false,
      // 树搜索
      treeQueryFocus: false, // 搜索输入框是否聚焦
      treeQueryMixins: '',
      // 树所需数据
      treeDataMixins: [],
      defaultCheckedNodeMixins: '', // 存放当前默认展开的节点的id
      defaultExpandKeysMixins: [] // 存放当前默认展开的节点的id
    }
  },
  methods: {
    getCurrentTreeStatus() {
      this.defaultCheckedNodeMixins = this.$refs[this.treeRefName].getCurrentKey()
    },
    handleRefreshTree() {
      this.defaultCheckedNodeMixins = this.$refs[this.treeRefName].getCurrentKey()
      const node = this.nodeInfo
      console.log('node', node)
      this.initTreeMixins().then(() => {
        this.setCurrentNode(node)
      })
    },
    /*
    *  节点展开时记录当前展开的节点
    */
    handleNodeExpand(data) {
      let flag = false
      this.defaultExpandKeysMixins.some(item => {
        if (item === data.id) {
          flag = true
          return true
        }
      })
      if (!flag) {
        this.defaultExpandKeysMixins.push(data.id)
      }
    },
    /**
     * 当前关闭节点下的所有节点id
     */
    collapseAllNodes(arr) {
      let result = []
      for (let i = 0; i < arr.length; i++) {
        result.push(arr[i].id)
        if (Array.isArray(arr[i].children)) {
          result = [...result, ...this.collapseAllNodes(arr[i].children)]
        }
      }
      return result
    },
    /**
     * 节点关闭时同步当前展开的节点
     * @param {*} data 该节点所对应的对象
     */
    handleNodeCollapse(data) {
      this.defaultExpandKeysMixins.forEach((item, index) => {
        if (item === data.id) {
          this.defaultExpandKeysMixins.splice(index, 1)
          // 删除关闭节点的下级已打开的节点id
          if (data.children && Array.isArray(data.children)) {
            this.defaultExpandKeysMixins.forEach(id => {
              // 1、判断关闭节点下级所有的节点id是否存在打开的节点id
              const isDel = this.collapseAllNodes(data.children).includes(id)
              // 2、如果有的话将它从已打开数组里面剔除
              isDel && (this.defaultExpandKeysMixins = this.defaultExpandKeysMixins.filter(k => k !== id))
            })
          }
          // console.log(6666666, data.children && this.collapseAllNodes(data.children))
          // console.log(8888888, data.children && [...new Set(this.collapseAllNodes(data.children))])
        }
      })
    },
    /*
    * 1. 清除当前选中节点，并设置 $attrs.node 为空
    */
    resetCurrentNode() {
      console.log('开始执行退出')
      this.nodeInfo = {}
      this.initTreeMixins().then(() => {
        this.$nextTick(() => {
          this.$refs[this.treeRefName].setCurrentKey()
        })
      })
    },
    /*
    * 2. 选中指定节点
    */
    setCurrentNode(data) {
      console.log('开始执行选中制定节点', data)
      // 1. 清空
      this.$nextTick(() => {
        this.$refs[this.treeRefName].setCurrentKey()
        // 2. 选中
        this.$nextTick(() => {
          this.$refs[this.treeRefName].setCurrentKey(data.id)
          // 右侧选中
          const node = this.$refs[this.treeRefName].getNode(data.id)
          this.handleNodeClick(node.data, node)
        })
      })
    },
    /*
    * 选中上一个节点
    */
    setPrevNode() {
      const curNode = this.$refs[this.treeRefName].getNode(this.nodeInfo.id)
      console.log('当前节点', curNode)
      if (curNode && curNode.parent) {
        console.log('当前父节点', curNode.parent)
        const node = curNode.parent.data
        this.defaultCheckedNodeMixins = node.id
        this.initTreeMixins().then(() => {
          this.setCurrentNode(node)
        })
      }
    }
  }
}
