import request from '@/apis/request.wms'

// 列表
export function getListApi(data) {
  return request({
    url: '/basic/material/group/pageList',
    method: 'post',
    data
  })
}
// 下拉列表
export function getPullListItemApi(data = {}) {
  return request({
    url: '/basic/material/group/listItem',
    method: 'post',
    data
  })
}
// 新增
export function getAddApi(data) {
  return request({
    url: '/basic/material/group/add',
    method: 'post',
    data
  })
}
// 修改
export function getEditApi(data) {
  return request({
    url: '/basic/material/group/edit',
    method: 'post',
    data
  })
}
// 删除
export function getDeleteApi(data) {
  return request({
    url: '/basic/material/group/delete',
    method: 'post',
    data
  })
}
// 物料组树列表
export function getAllTreeDataApi(data = {}) {
  return request({
    url: '/basic/material/group/treeList',
    method: 'post',
    data,
    timeout: 2 * 60 * 1000
  })
}

// 物料组分页列表
export function getGroupListApi(data = {}) {
  return request({
    url: '/basic/material/group/pageList',
    method: 'post',
    data
  })
}
// 绑定物料
export function bindGroupApi(data = {}) {
  return request({
    url: '/basic/material/group/bindMaterial',
    method: 'post',
    data
  })
}

// 查询物料组下物料列表
export function getListByGroupIdApi(data = {}) {
  return request({
    url: '/basic/wmsMaterial/listByGroupId',
    method: 'post',
    data
  })
}

