<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="物料组描述" prop="description">
          <el-input v-model.trim="form.description" placeholder="请输入物料组描述" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="物料组编号" prop="code">
          <el-input v-model.trim="form.code" placeholder="请输入物料组编号" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="物料组类型" prop="type">
          <el-select
            v-model="form.type"
            filterable
            clearable
            placeholder="请选择物料组类型"
            style="width: 178px;"
          >
            <el-option
              v-for="item in dict['wms_material_group_type']"
              :key="item.id"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>
  </el-drawer>
</template>

<script>
import { getAddApi as addApi, getEditApi as updataApi } from '@/apis/modules/base/baseItemGroup'
import QueryItemMixin from '@/mixins/queryItem'
const INIT_FORM = () => ({
  description: '',
  code: '',
  abbName: '',
  type: ''
})

export default {
  name: 'CustomerInfoDialogComs',
  mixins: [
    QueryItemMixin
  ],
  dicts: ['wms_material_group_type'],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      loading: false,
      rules: {
        description: [
          { required: true, message: '请输入物料组描述', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '请输入物料组编号', trigger: 'blur' }
        ]
      },
      gradeOpts: []
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = false
          const api = { add: addApi, modify: updataApi }[this.editType]
          const baseParams = {
            description: this.form.description,
            code: this.form.code,
            abbName: this.form.abbName,
            type: this.form.type
          }
          api({
            ...baseParams,
            ...this.form.rid && { id: this.form.rid }
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}成功`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    initSelect() {},
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM() })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: calc(100vh - 150px);
    // overflow: auto;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  ::v-deep .el-drawer {
    width: 640px;
  }
  .drawer-footer{
    text-align: right;
  }
  </style>

