<template>
  <div>
    <el-row class="clearfix">
      <el-button type="primary" icon="el-icon-plus" :disabled="level !== 3" @click="handleBindMaterial ">物料绑定</el-button>
      <el-button type="warning" icon="el-icon-edit" :disabled="!+node.rid" @click="handleModify">修改</el-button>
      <el-button type="danger" icon="el-icon-delete" :disabled="!+node.rid" @click="handleDelete">删除</el-button>
    </el-row>
    <transition v-if="level !== 3" name="fade-transform" mode="out-in">
      <el-empty :description="`请选择小类物料节点`" />
    </transition>
    <template v-else>
      <el-row>
        <table-com
          :loading="loading"
          :index="true"
          :selection="false"
          :table-column-sort="false"
          :column-list="columnList"
          :table-data="tableData"
          :show-operate="false"
          :refresh-key="refreshKey"
          @rowClick="handleRowClick"
          @refresh="searchInit"
        />
      </el-row>
      <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    </template>
    <!-- 修改抽屉 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="dialogType"
      :title="dialogTitle"
      @submit="handleSubmit"
    />
    <!-- :selection="tableData.map(item => item.id)" -->
    <!-- 物料弹窗 -->
    <material-select
      v-model="visible"
      :selected-init="false"
      :extra-info="{orderBy: 'inventoryNum desc', viewBind: false}"
      @confirm="onSelectConfirm"
    />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getListByGroupIdApi, getDeleteApi, bindGroupApi } from '@/apis/modules/base/baseItemGroup'
import EditDialog from './Dialog'
import MaterialSelect from '@/components/ModalSelect/MaterialForm'

export default {
  name: '',
  components: {
    EditDialog,
    MaterialSelect
  },
  mixins: [paginationPage],
  props: {
    node: {
      type: Object,
      default: () => {}
    },
    level: {
      type: [String, Number],
      default: 1
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      refreshKey: 1,
      tableData: [],
      columnList: [
        {
          module: 'link',
          prop: 'code',
          label: '物料编号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          handleClick: (row, index) => {
            this.$router.push({
              name: 'WMSBaseMaterialInformation',
              params: {
                code: row.code
              }
            })
          }
        },
        {
          module: 'word',
          prop: 'description',
          label: '物料名称',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      dialogForm: {},
      dialogType: 'modify',
      dialogVisible: false,
      visible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.dialogType === 'add') {
        return `新增物料组`
      } else {
        return `编辑物料组`
      }
    }
  },
  watch: {
    'node.rid': {
      handler: function(val) {
        if (this.level === 3) this.init()
      },
      immediate: true
    }
  },
  methods: {
    init() {
      return new Promise((resolve, reject) => {
        const params = {
          groupId: this.node.rid
        }
        this.loading = true
        getListByGroupIdApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            this.refreshKey = new Date().getTime()
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleSubmit() {
      this.init()
      this.$emit('refreshTree')
    },
    // 物料绑定
    handleBindMaterial() {
      this.visible = true
    },
    // 删除树节点
    handleDelete() {
      this.$confirm(`此操作将永久删除该物料组, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        getDeleteApi({
          id: this.node.rid
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.$emit('setPrevNode')
          }
        })
      }).catch(() => {
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    // 修改树节点
    handleModify() {
      this.dialogForm = { ...this.node }
      this.dialogType = 'modify'
      this.dialogVisible = true
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    onSelectConfirm(list) {
      if (!list.length) return
      return new Promise((resolve, reject) => {
        bindGroupApi({ groupId: this.node.rid, materialIds: list.map(item => item.id) })
          .then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '成功',
                message: '物料绑定成功'
              })
              this.init()
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
